import React, { useEffect, useState } from 'react'
import { useResizeDetector } from 'react-resize-detector'
// import { ProgressiveImage } from 'react-progressive-graceful-image'
import { motion } from 'framer-motion/dist/framer-motion'

export const Moment = ({images}) => {
  const [idx, setIdx] = useState(0)
  const { width, height, ref } = useResizeDetector();
  
  return <div 
    // className="bg" 
    style={{
      // backgroundImage: `url(${props.images[idx].original})` 
    }}
    ref={ref}
    >{`${width}x${height}x${idx}`}
    {/* <ProgressiveImage src={props.images[idx].original} placeholder={props.images[idx].thumbnail}>
      {(src) => <img  src={src} alt="an" />}
    </ProgressiveImage> */}
    <motion.button style={{
        width: '100%',
        height: '100%',
        objectFit: 'contain',
        overflow: 'hidden',
        userSelect: 'none',
        '-moz-user-select': 'none',
        '-webkit-user-select': 'none',
        '-ms-user-select': 'none'
      }}
      whileTap={{ scale: 0.9 }} >
      <img style={{
        width: '100%',
        height: '100%',
        objectFit: 'contain',
        overflow: 'hidden',
        userSelect: 'none',
        '-moz-user-select': 'none',
        '-webkit-user-select': 'none',
        '-ms-user-select': 'none'
      }}  onClick={()=>setIdx(images.length > idx + 1 ? idx + 1 : 0)} src={images[idx].original} alt=''/>
    </motion.button>
  </div>;
}
export default Moment